import React from 'react';
import {render} from 'react-dom';
import {Link } from 'react-router';
import '../style/menu.css';

export default  class Menu extends React.Component{
    render(){
        return (
            <div className="menuNav">
                <div className ="login_href">
                    <Link to = '/end/login' className="login"></Link>
                    <Link to = '/end/register'></Link>
                </div>
                <div className = 'title'>
                    <Link to = '/' className = 'photo'>
                        <img src={require('../images/logo1.jpg')} style={{width:20 + 'px', height: 10 + 'px'}}/>
                    </Link>
                    <ul className = "rui-nav rui-bar">
                        <li><Link to='/'></Link></li>
                        <li><Link to='/introduction'></Link></li>
                        <li><Link to='/center'></Link></li>
                        <li><Link to='/cultural'></Link></li>
                        <li><Link to='/product'></Link></li>
                        <li><Link to='/message'></Link></li>
                        <li><Link to='/contact'></Link></li>
                    </ul>
                </div>
            </div>
        )
    }
}


